import React from "react";
import { inject, observer } from "mobx-react";
import { get, observable } from "mobx";
import ThumbnailCom from "../../common/Thumbnail/ThumbnailCom";
import { getFile, os } from "../../util/index";

@inject("albumdetail")
@observer
class Thumbnail extends React.PureComponent {
  @observable imgBackground = {};

  componentDidMount() {
    const { columnIndex, data, rowIndex } = this.props;
    const item =
      data[rowIndex * this.props.albumdetail.columnCount + columnIndex];
    if (item) {
      let url = getFile.file_s(item.id);
      this.imgBackground = {
        background: `#d3d3d3 url(${url}) center center no-repeat`,
        backgroundSize: "cover",
      };
    }
  }

  UNSAFE_componentWillReceiveProps(next) {
    const { columnIndex, data, rowIndex } = next;
    const item =
      data[rowIndex * this.props.albumdetail.columnCount + columnIndex];
    if (item) {
      let url = getFile.file_s(item.id);
      this.imgBackground = {
        background: `#d3d3d3 url(${url}) center center / cover no-repeat`,
      };
    }
  }

  render() {
    const { columnIndex, data, rowIndex, style } = this.props;
    const {
      columnCount,
      checkclick,
      b_img_show,
      removeFileids,
    } = this.props.albumdetail;
    const item = data[rowIndex * columnCount + columnIndex];
    let fileNum = 0;
    removeFileids.forEach((item) => {
      if (!item.true) {
        fileNum += 1;
      }
    });
    try {
      if (get(item, "true")) {
        return <div style={style}></div>;
      } else {
        return (
          <ThumbnailCom
            item={item}
            style={style}
            num={fileNum}
            checkclick={checkclick}
            b_img_show={b_img_show}
            index={rowIndex * columnCount + columnIndex}
            os={os().isPhone || os().isAndroid || os().isTablet}
            imgBackground={this.imgBackground}
          />
        );
      }
    } catch (error) {
      return <div style={style}></div>;
    }
  }
}

export default Thumbnail;
