import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import { get, observable, remove, set } from "mobx";
import dayjs from "dayjs";
import { FixedSizeGrid as Grid } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import { withTranslation } from "react-i18next";
import "./style.scss";
import { Button, Dropdown, Icon, Menu, message } from "antd";
import Thumbnail from "./Thumbnail";
import Upinput from "../../common/Upinput";
import Loading from "../../common/Widget/Loading";
import Nodata from "../../common/Widget/Nodata";
import Information from "../../common/Information";
import { wsget } from "../../ws";
import Goblack from "../../common/Goblack";
import Operation from "../../common/Operation";
import DropUp from "../../common/DropUp";
import { getFile, os, selfAdaption } from "../../util/index";
import CustomModal from "../../common/CustomModal";
import ListCell from "../../common/ListCell";
import ThumbnailCom from "../../common/Thumbnail/ThumbnailCom";
import ImageGallery from "../../common/ImageGallery";

@withTranslation()
@inject("photoalbum", "uploadFiles", "login", "albumdetail")
@observer
class Albumdetails extends Component {
  constructor(props) {
    super(props);
    this.contentRef = React.createRef();
    this.daughterRef = React.createRef();
    this.albumListRef = React.createRef();
  }
  @observable dropVisible = false;
  @observable num = 100; //一页加载个数
  @observable init = false;
  @observable albumdetailList = []; //相册详情列表
  @observable removeFileids = []; //选中的图片列表
  @observable checkAll = false; //是否全选
  @observable visible = false; //大图modul
  @observable imgfile = {}; //大图文件
  @observable imgidx = null; //切换图片的标识
  @observable info_visible = false; //详情显隐
  @observable list = []; //渲染的数据
  @observable filenum = 0; //文件数量
  @observable page = 0; //第一条数据
  @observable hasMoreItems = true;
  @observable number = this.num;

  @observable infoObj = null; //详情数据
  @observable name = "";
  @observable id = null;
  @observable created = null;

  @observable addBumInit = false;
  @observable addBumDetInit = false;
  @observable otherVisible = false;
  @observable okBtnDis = true;
  @observable albumList = [];
  @observable detailList = [];
  @observable addItemClick = null;
  @observable renderItem = 0;
  @observable addSelNum = 0;

  initialData = [];

  async componentDidMount() {
    try {
      const { id } = this.props.match.params;
      const { name, created, updated } = this.props.photoalbum.urlData;

      this.id = Number(id);
      if (!name) {
        const albumArr = await wsget("album_list", null);
        const pos = albumArr.findIndex((item) => item.id === Number(id));
        if (pos === -1) {
          this.props.history.push("/album");
        }
        this.name = albumArr[pos].name;
        this.created = albumArr[pos].created;
        this.props.albumdetail.update(
          albumArr[pos].updated || albumArr[pos].created
        );
      } else {
        this.name = name;
        this.created = created;
        this.props.albumdetail.update(updated);
      }
      this.props.albumdetail.getData(this.id, this.getWH, this.getH);
      window.addEventListener("resize", this.myresize);
    } catch (error) {
      console.log(error);
      this.props.history.push("/album");
    }

    document.addEventListener("dragenter", this.ondragenter);

    document.addEventListener("dragleave", this.ondragleave);

    document.addEventListener("dragover", this.ondragover);

    document.addEventListener("drop", this.ondragover);
  }

  componentWillUnmount() {
    this.props.albumdetail.clearData();
    window.removeEventListener("resize", this.myresize);
    document.removeEventListener("dragenter", this.ondragenter);
    document.removeEventListener("dragleave", this.ondragleave);
    document.removeEventListener("dragover", this.ondragover);
    document.removeEventListener("drop", this.ondragover);
  }

  //拖拽上传
  ondragenter = (e) => {
    this.elemetnNode = e.target;
    if (!this.dropVisible) {
      this.dropVisible = true;
    }
  };
  ondragleave = (e) => {
    if (this.elemetnNode === e.target) {
      this.dropVisible = false;
    }
  };
  ondragover = (e) => {
    e.preventDefault(); //阻止浏览器读取文件
  };
  acceptedFiles = (Files) => {
    const { id } = this;
    this.dropVisible = false;
    this.props.uploadFiles.upFile(Files, id, null, "album");
  };
  //end

  //计算宽高
  getWH = (flag) => {
    try {
      const { wi, hi, colNum } = selfAdaption(
        this.contentRef.current.offsetWidth,
        165,
        150,
        100,
        100,
        597
      );
      this.childW = wi;
      this.childH = hi;
      this.columnCount = colNum;
      if (!flag) {
        this.getH();
      }
      return this.columnCount;
    } catch (error) {
      console.log(error);
      // this.getWH();
    }
  };

  getH = () => {
    const { albumdetail } = this.props.albumdetail;
    this.rowCount = albumdetail.length / this.columnCount;
  };

  myresize = () => {
    this.props.albumdetail.resize(this.getWH);
  };

  //详情显示
  info_show = () => {
    const infoObj = [
      {
        id: 1,
        icon: "file-image",
        txt: "Photo",
        data: this.props.albumdetail.filenum,
      },
      {
        id: 2,
        icon: "clock-circle",
        txt: "CreationTime",
        data: dayjs(this.created).format(this.props.t("LL")),
      },
      {
        id: 3,
        icon: "clock-circle",
        txt: "LastUpdate",
        data: dayjs(this.props.albumdetail.updated).format(this.props.t("LL")),
      },
    ];
    this.info_visible = true;
    this.infoObj = infoObj;
  };

  //上传
  uploading = (e, reset) => {
    const { id } = this;
    this.props.uploadFiles.upFile(e.target.files, id, reset, "album");
  };

  goblack = () => {
    this.props.history.push("/album");
  };

  //其余相册添加
  cloudAlbumUpload = () => {
    this.otherVisible = true;
    this.addBumInit = false;
    wsget("album_list", null).then((data) => {
      const { id } = this.props.match.params;
      const arr = data.filter((el) => el.id !== Number(id));
      const { wi, hi, colNum } = selfAdaption(
        this.albumListRef.current.offsetWidth,
        200,
        220,
        180,
        200
      );
      this.addChildW = wi;
      this.addChildH = hi;
      this.addColnum = colNum;
      const countRow = arr.length / this.addColnum;
      this.addRowCount = Math.ceil(countRow);
      if (arr.length % this.addColnum !== 0) {
        const leng = this.addColnum * this.addRowCount - arr.length;
        for (let index = 0; index < leng; index++) {
          arr.push({ true: true });
        }
      }
      this.albumList = arr;
      this.addBumInit = true;
    });
  };

  goAlbumdetail = (item) => {
    this.addBumInit = false;
    this.okBtnDis = false;
    this.renderItem = 1;
    this.addItemClick = item;
    wsget("album_detail_mini", item.id).then((data) => {
      const { wi, hi, colNum } = selfAdaption(
        this.albumListRef.current.offsetWidth,
        130,
        130,
        100,
        100,
        597
      );
      this.addChildW = wi;
      this.addChildH = hi;
      this.addColnum = colNum;
      const countRow = data.length / this.addColnum;
      this.addRowCount = Math.ceil(countRow);
      if (data.length % this.addColnum !== 0) {
        const leng = this.addColnum * this.addRowCount - data.length;
        for (let index = 0; index < leng; index++) {
          data.push({ true: true });
        }
      }
      this.detailList = data;
      this.addBumInit = true;
    });
  };

  renderDetails = ({ columnIndex, data, rowIndex, style }) => {
    const item = data[rowIndex * this.addColnum + columnIndex];
    try {
      if (get(item, "true")) {
        return <div style={style}></div>;
      } else {
        return (
          <ThumbnailCom
            item={item}
            style={style}
            num={1}
            checkclick={this.checkclick}
            b_img_show={this.checkclick}
            index={rowIndex * this.addColnum + columnIndex}
            os={os().isPhone || os().isAndroid || os().isTablet}
            imgBackground={{
              background: `#d3d3d3 url(${getFile.file_s(
                item.id
              )}) center center no-repeat`,
              backgroundSize: "cover",
            }}
          />
        );
      }
    } catch (error) {
      return <div style={style}></div>;
    }
  };

  renderLists = ({ columnIndex, data, rowIndex, style }) => {
    const item = data[rowIndex * this.addColnum + columnIndex];
    return (
      <ListCell
        style={style}
        timeName={`${this.props.t("CreationTime")}：`}
        item={item}
        goPlaylistdetails={this.goAlbumdetail}
        album={true}
        frameNumDis={true}
      />
    );
  };

  checkclick = (e, item) => {
    e.stopPropagation();
    if (item.check) {
      remove(item, "check");
      this.addSelNum -= 1;
    } else {
      set(item, { check: true });
      this.addSelNum += 1;
    }
  };

  addGoback = () => {
    this.okBtnDis = true;
    const list = this.albumList.filter((item) => {
      return !item.true;
    });
    const { wi, hi, colNum } = selfAdaption(
      this.albumListRef.current.offsetWidth,
      200,
      220,
      180,
      200
    );
    this.addChildW = wi;
    this.addChildH = hi;
    this.addColnum = colNum;
    const countRow = list.length / this.addColnum;
    this.addRowCount = Math.ceil(countRow);
    this.addSelNum = 0;
    this.renderItem = 0;
  };

  addCheckAll = () => {
    const list = this.detailList.filter((item) => {
      return !item.true;
    });
    if (this.addSelNum === list.length) {
      this.detailList.forEach((item) => {
        if (item.id) {
          remove(item, "check");
        }
      });
      this.addSelNum = 0;
    } else {
      this.addSelNum = 0;
      this.detailList.forEach((item) => {
        if (item.id) {
          set(item, { check: true });
          this.addSelNum += 1;
        }
      });
    }
  };

  addInvert = () => {
    this.addSelNum = 0;
    this.detailList.forEach((item) => {
      if (item.id) {
        if (item.check) {
          remove(item, "check");
        } else {
          set(item, { check: true });
          this.addSelNum += 1;
        }
      }
    });
  };

  addhandleOk = () => {
    const { id } = this.props.match.params;
    const list = [];
    this.detailList.forEach((item) => {
      if (item.check) {
        const obj = {
          id: item.id,
        };
        if (item.cpt) {
          obj.cpt = item.cpt;
        }
        list.push(obj);
      }
    });
    if (!list.length) {
      message.error(this.props.t("Noselectedpic"));
      return;
    }
    this.addCancel();
    const data = {
      id,
      list,
    };
    wsget("album_add_files", data, 1).then((res) => {
      this.props.albumdetail.getData(this.id, this.getWH, this.getH);
    });
  };

  addCancel = () => {
    this.okBtnDis = true;
    this.otherVisible = false;
    this.addSelNum = 0;
    this.renderItem = 0;
  };

  render() {
    const { t } = this.props;
    const { info_visible, name, created } = this;
    const {
      init,
      albumdetail,
      filenum,
      removeFileids,
      oncheckAll,
      remove_img,
      closeDialog,
      visible,
      imgfile,
      onPiece,
      nextPage,
      addCpt,
      invert,
      updated,
      cancelAll,
    } = this.props.albumdetail;

    let fileNum = 0;
    removeFileids.forEach((item) => {
      if (!item.true) {
        fileNum += 1;
      }
    });

    const menu = (
      <Menu>
        <Menu.Item key="0">
          <div className="cloudAlbumUpload">
            <Icon type="plus" style={{ fontSize: "22px" }} />
            <Upinput className="upphoto-btn up_btn" onupload={this.uploading} />
          </div>
        </Menu.Item>
        <Menu.Item key="1">
          <span className="cloudAlbumUpload" onClick={this.cloudAlbumUpload}>
            <Icon type="picture" style={{ fontSize: "22px" }} />
            <span>{t("albumAdd")}</span>
          </span>
        </Menu.Item>
      </Menu>
    );

    const empty = this.renderItem
      ? this.detailList.length
      : this.albumList.length;

    return (
      <div className="Albumdetails">
        <div className="Albumdetails-header">
          <Goblack go={this.goblack} name={name} />
          <div className="a-details-information">
            {/* <Upinput className="upphoto-btn" onupload={this.uploading} /> */}
            <Dropdown overlay={menu} trigger={["hover", "click"]} size="large">
              <Button
                type="primary"
                className="Album_up_btn"
                style={{ padding: "0 25px" }}
              >
                {t("Uploadphotos")} <Icon type="down" />
              </Button>
            </Dropdown>
            <div className="Album-file-num">
              <div className="a-d-i-icon">
                <i className="iconfont icon">&#xe785;</i>
              </div>
              <div className="Album-num-text">
                <p className="text">{t("Photo")}</p>
                <p className="num">{filenum}</p>
              </div>
            </div>
            <div className="Album-creation-time">
              <div className="a-d-i-icon">
                <span className="creation-time-icon"></span>
              </div>
              <div className="Album-num-text">
                <p className="text">{t("CreationTime")}</p>
                <p className="num">
                  {created && dayjs(created).format(t("LL"))}
                </p>
              </div>
            </div>
            <div className="Album-last-update">
              <div className="a-d-i-icon">
                <span className="last-update-icon"></span>
              </div>
              <div className="Album-num-text">
                <p className="text">{t("LastUpdate")}</p>
                <p className="num">
                  {updated && dayjs(updated).format(t("LL"))}
                </p>
              </div>
            </div>
            <div className="Album-info-btn" onClick={this.info_show}>
              <Icon type="exclamation" />
            </div>
          </div>
        </div>

        <Operation
          selectNum={fileNum}
          dataNum={albumdetail.filter((el) => el.id).length}
          oncheck={oncheckAll}
          invert={invert}
          remove={remove_img}
          cancelAll={cancelAll}
        />

        <div ref={this.contentRef} className="Albumdetails-main">
          {init ? (
            albumdetail.length > 0 ? (
              <AutoSizer>
                {({ height, width }) => (
                  <Grid
                    className="Grid"
                    itemData={albumdetail}
                    rowCount={this.rowCount}
                    columnCount={this.columnCount}
                    columnWidth={this.childW}
                    height={height}
                    rowHeight={this.childH}
                    width={width}
                  >
                    {Thumbnail}
                  </Grid>
                )}
              </AutoSizer>
            ) : (
              <Nodata />
            )
          ) : (
            <Loading />
          )}
        </div>

        {this.dropVisible && (
          <DropUp
            title={t("UploadToList")}
            acceptedFiles={this.acceptedFiles}
          />
        )}

        {
          visible && (
            <ImageGallery
              id={imgfile.id}
              cpt={imgfile.cpt}
              ext={imgfile.ext}
              closeDialog={closeDialog}
              onPiece={onPiece}
              nextPage={nextPage}
              blur={addCpt}
            />
          )
        }

        <Information
          visible={info_visible}
          data={this.infoObj}
          handleCancel={() => {
            this.info_visible = false;
          }}
        />

        <CustomModal
          visible={this.otherVisible}
          title={t("albumAdd")}
          okBtnDis={this.okBtnDis}
          handleOk={this.addhandleOk}
          handleCancel={this.addCancel}
        >
          <>
            {this.renderItem ? (
              <div style={{ position: "relative" }}>
                <Goblack
                  go={this.addGoback}
                  name={this.addItemClick.name}
                  style={{ paddingRight: "180px", paddingLeft: "10px" }}
                />
                <div className="add_operation">
                  <Operation
                    selectNum={this.addSelNum}
                    dataNum={this.detailList.filter((el) => el.id).length}
                    oncheck={this.addCheckAll}
                    invert={this.addInvert}
                    style={{ border: "0", padding: "0" }}
                  />
                </div>
              </div>
            ) : null}
            <div
              style={{ height: this.renderItem ? "61vh" : "65vh" }}
              className="add_toBum_main"
              ref={this.albumListRef}
            >
              {this.addBumInit ? (
                empty ? (
                  <AutoSizer>
                    {({ height, width }) => (
                      <Grid
                        className="Grid"
                        itemData={
                          this.renderItem ? this.detailList : this.albumList
                        }
                        rowCount={this.addRowCount}
                        columnCount={this.addColnum}
                        columnWidth={this.addChildW}
                        height={height}
                        rowHeight={this.addChildH}
                        width={width}
                      >
                        {this.renderItem
                          ? this.renderDetails
                          : this.renderLists}
                      </Grid>
                    )}
                  </AutoSizer>
                ) : (
                  <Nodata />
                )
              ) : (
                <Loading />
              )}
            </div>
          </>
        </CustomModal>
      </div>
    );
  }
}

export default Albumdetails;
